@-webkit-keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.4;
  }
}

@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.4;
  }
}

.gallery {
  position: fixed;
  top: 0;
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.backdrop {
  position: fixed;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: black;
  opacity: 0.4;
  -webkit-animation: 1s ease-out 0s opacity;
          animation: 1s ease-out 0s opacity;
}

.gallery img {
  max-width: 100%;
  max-height: 80%;
}


